@import "sass/index";

.root {
  width: 100%;
  height: 100vh;
}

.frame {
  width: 100%;
  height: 100%;

  & html {
    overflow: hidden;
  }
}

.container {
}

$radius: 30px;

.topWrapper {
  display: flex;
  justify-content: space-between;

  border-top-left-radius: $radius;
  border-top-right-radius: $radius;
}
.bottomWrapper {
  border-bottom-left-radius: $radius;
  border-bottom-right-radius: $radius;
}

.frameLine {
  width: 100%;
  padding: 15px;
  background: use(--info-dark);
}

// *** Switch:
.toggle {
  width: 22px;
  height: 22px;
}

.track {
  height: 20px;
  width: 90%;
}

.switchText {
  opacity: 1;
  text-transform: capitalize;

  font-family: "Gilroy";
  font-weight: 600;
  font-size: 16px;
}

.switchRoot {
  justify-content: flex-end;

  @media (max-width: 380px) {
    width: 100%;
  }
}

.playMode {
  color: use(--primary-light);

  @media (max-width: 380px) {
    display: none;
  }
}

.playCoin {
  position: static;
  margin-bottom: -3px;
  margin-left: 5px;
}
