@import "sass/index";

.root {
  width: 100%;
  height: 100%;
  background: url(./background.webp) no-repeat;
  background-size: cover;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  font-family: "Gilroy";
}

.text {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  text-align: center;

  color: #ffffff;

  max-width: 670px;
  display: block;
  margin: 0 auto 20px;
  padding: 0 10px;
}

.buttonWrapper {
}
.button {
  background: #34e7ff !important;
  padding: 10px 25px;
  border-radius: 25px;

  margin: 5px;

  font-weight: 600;
  font-size: 16px;
  text-transform: capitalize;
  line-height: 1;

  color: #000000;

  & svg {
    margin-right: 5px;
  }
}
