@import "sass/index";

.root {
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.likesWrapper {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: use(--primary-light);

  cursor: pointer;
  user-select: none;
}

.favorites {
  margin-left: 10px;
}

.buttonWrapper {
  width: fit-content;
}
.button {
  padding: 0;
  color: use(--primary-light);
  border-radius: 0;
  background: transparent !important;
  box-shadow: none;

  &:nth-child(2) {
    margin: 0 30px;
  }
}

.popper {
  padding: 10px;
  border-radius: 10px;
  color: white;
  font-weight: 600;
  font-size: 16px;
}

.star {
}
.fillStar {
  stroke: use(--primary-light);
  color: transparent;
}
.strokeStar {
  stroke: transparent;
  color: use(--primary-light);
}
